/* Reset */
html,
body,
div {
  border: 0;
  margin: 0;
  padding: 0;
}

/* Box-sizing border-box */
* {
  box-sizing: border-box;
}

/* Set up a default font and some padding to provide breathing room */
body {
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #fafafa;
}

p {
  font-weight: 400;
  letter-spacing: 0.01em;
  line-height: 20px;
  margin-bottom: 1em;
  margin-top: 2.4em;
  color: #000;
}

ul {
  margin: 10px 0 0;
  padding: 0 0 0 20px;
}

li {
  font-weight: 400;
  margin-top: 4px;
}

button {
  background-color: #106cc8;
  border-style: none;
  color: rgba(255, 255, 255, 0.87);
  cursor: pointer;
  display: inline-block;
  font-size: 14px;
  height: 40px;
  padding: 8px 18px;
  text-decoration: none;
}
a {
    font-size: 14px;
    margin-top: 1em;
    color: gray;
}
$primary-color: #05c9ce;
button:hover {
  //background-color: #28739e;
}
.btn-primary {
    background-color: $primary-color;
    border-radius: 40px;
    color: #fff;
    margin-top: 0.7em;
    //border-color: #46b8da;
    &:hover {
      color: #fff;
      background-color: #31b0d5;
      //border-color: #269abc;
    }
}
form {
    margin-top: 2em;
}
.input-lg{
    height: 43px;
    font-size: 16px;
}
.container{
    text-align: center; 
    background-color: #fff;
    padding-bottom: 2.2em;
    margin-top: 1em;
    box-shadow: 0px 5px 15px 8px #eee;
    border-radius: 7px;
}
h2{
    &.welcome{
         color: $primary-color;
     }
    &.text-title{
         color: $primary-color;
     }
}
p{
    &.welcome{
         color: #626262;
     }
}
img{
    &.welcome{
        margin: auto;
    }
}
.row.step1{
    background: #303030;
    height: 8em;
    display: flex;
    flex-direction: column-reverse;
    text-align: center;
    padding-bottom: 10px;
    img{
        max-width: 10%;
        display: inline; 
        vertical-align: bottom;
    }
    span{
        color: #fafafa;
        font-size: 1.2em;
    }
    
}
.planes{
    text-align: center;
    strong{
        color: $primary-color;
    }
}
.img-credit-card{
    width: 50%;
    vertical-align: super;
}
.separador{
    height: 3px;
    background: $primary-color;   
}

